.loading-icon {
  position: relative;
  display: grid;
  grid-template-columns: 33% 33% 33%;
  grid-gap: 2px;
  width: 100px;
  height: 100px;
  -webkit-transition: opacity 0.25;
  transition: opacity 0.25; }
  .loading-icon > div {
    position: relative;
    width: 100%;
    height: 100%;
    background: tomato;
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-transform-origin: center center;
            transform-origin: center center;
    -webkit-animation: loader 2s infinite linear;
            animation: loader 2s infinite linear; }
    .loading-icon > div:nth-of-type(1), .loading-icon > div:nth-of-type(5), .loading-icon > div:nth-of-type(9) {
      -webkit-animation-delay: 0.4s;
              animation-delay: 0.4s; }
    .loading-icon > div:nth-of-type(4), .loading-icon > div:nth-of-type(8) {
      -webkit-animation-delay: 0.2s;
              animation-delay: 0.2s; }
    .loading-icon > div:nth-of-type(2), .loading-icon > div:nth-of-type(6) {
      -webkit-animation-delay: 0.6s;
              animation-delay: 0.6s; }
    .loading-icon > div:nth-of-type(3) {
      -webkit-animation-delay: 0.8s;
              animation-delay: 0.8s; }

@-webkit-keyframes loader {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  80% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0); } }

@keyframes loader {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  80% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0); } }

